import React, { useState, useEffect } from 'react';
import { graphql } from 'gatsby';
import Img from 'gatsby-image';
import { useInView } from 'react-intersection-observer';
import { Link } from 'gatsby';

import Layout from '../components/layout';
import SEO from '../components/seo';
import Disqus from '../components/Disqus';
import Tags from '../components/Tags';
import Breadcrumb from '../components/Breadcrumb';
import Toc from '../components/Toc';
import ShareButton from '../components/ShareButton';

// import pick from '../utils/pick';
import '../utils/normalize.css';
import '../utils/css/post.scss';
import '../utils/css/global.scss';

function BlogPostTemplate({ pageContext, location, data }) {
    const post = data.markdownRemark;
    const siteTitle = data.site.siteMetadata.title;
    const siteUrl = data.site.siteMetadata.siteUrl;
    // const headings = pick(post, 'headings') || [];
    const [showDisqus, setShowDisqus] = useState(false);
    const { ref, inView } = useInView({
        /* Optional options */
        threshold: 0,
        rootMargin: '500px 0% 500px 0%'
    });
    const { ref: imageRef, inView: imageInView } = useInView({
        /* Optional options */
        threshold: 0
    });
    const { ref: breadRef, inView: breadInView } = useInView({
        /* Optional options */
        threshold: 0
    });
    useEffect(() => {
        if (inView) {
            setShowDisqus(true);
        }
    }, [inView]);
    const facebook = data.site.siteMetadata.social.facebook;
    const { previous, next } = pageContext;
    const categories = post.frontmatter.categories;
    const categoryUrl = `${siteUrl}/categories/${post.frontmatter.categories}`;
    return (
        <Layout location={location} facebook={facebook} title={siteTitle} facebook={facebook}>
            <SEO
                title={post.frontmatter.title}
                description={post.frontmatter.description || post.excerpt}
                url={`${siteUrl}${location.pathname}`}
                date={post.frontmatter.date}
                modifyDate={post.parent.modifiedTime}
                category={categories}
                categoryUrl={categoryUrl}
                image={
                    post.frontmatter.thumbnail &&
                    `${siteUrl}${post.frontmatter.thumbnail.childImageSharp.fluid.src}`
                }
            />
            <div className={`post-header ${post.frontmatter.thumbnail || `no-image`}`}>
                <header className="post-content-header">
                    <h1 className="post-content-title">{post.frontmatter.title}</h1>
                </header>
                {post.frontmatter.thumbnail && (
                    <div className="post-content-image" ref={imageRef}>
                        <Img
                            className="kg-image"
                            fluid={post.frontmatter.thumbnail.childImageSharp.fluid}
                            alt={post.frontmatter.title}
                        />
                    </div>
                )}
                <div className="bread-container">
                    <Breadcrumb
                        category={categories}
                        categoryUrl={categoryUrl}
                        title={post.frontmatter.title}
                        url={`${siteUrl}${location.pathname}`}
                    />
                </div>
                <div className="info-container" ref={breadRef}>
                    <div className="info">
                        <span className="post-date">{post.frontmatter.date}</span>·
                        <span className="post-reading-time">
                            {post.timeToRead}
                            min Read
                        </span>
                    </div>
                    <ShareButton
                        url={`${siteUrl}${location.pathname}`}
                        title={post.frontmatter.title}
                        description={post.frontmatter.description}
                    />
                </div>
            </div>
            <div>
                <div className={`toc-container ${!imageInView && breadInView ? `show-toc` : ''}`}>
                    <Toc list={post.headings} />
                </div>
                <div className="post-content">
                    <article
                        className="post-content-body"
                        dangerouslySetInnerHTML={{ __html: post.html }}
                    />
                    <div ref={ref}></div>
                    <Tags list={post.frontmatter.tags} />
                    <div className="related-container">
                        <div className="prev">
                            {previous && (
                                <Link to={`/${previous.fields.slug.split('-')[2]}`} rel="prev">
                                    ← {previous.frontmatter.title}
                                </Link>
                            )}
                        </div>
                        <div className="next">
                            {next && (
                                <Link to={`/${next.fields.slug.split('-')[2]}`} rel="next">
                                    {next.frontmatter.title} →
                                </Link>
                            )}
                        </div>
                    </div>
                    {showDisqus && (
                        <Disqus
                            url={`${siteUrl}${location.pathname}`}
                            identifier={location.pathname.slice(1)}
                            title={post.frontmatter.title}
                        />
                    )}
                </div>
                {/* <footer className="post-content-footer">
                    There are two options for how we display the byline/author-info.
        If the post has more than one author, we load a specific template
        from includes/byline-multiple.hbs, otherwise, we just use the
        default byline. 
                </footer> */}
            </div>
        </Layout>
    );
}

export default BlogPostTemplate;

export const pageQuery = graphql`
    query BlogPostBySlug($slug: String!) {
        site {
            siteMetadata {
                title
                author
                siteUrl
                social {
                    facebook
                }
            }
        }
        markdownRemark(fields: { slug: { eq: $slug } }) {
            id
            fields {
                slug
            }
            headings {
                value
                id
                depth
            }
            excerpt(pruneLength: 160)
            html
            frontmatter {
                title
                date(formatString: "MMMM DD, YYYY")
                description
                categories
                tags
                thumbnail {
                    childImageSharp {
                        fluid(maxWidth: 1360) {
                            ...GatsbyImageSharpFluid
                        }
                    }
                }
            }
            timeToRead
            parent {
                ... on File {
                    modifiedTime
                }
            }
        }
    }
`;
