import React from 'react';
import { Link } from 'gatsby';

function Tags({ list = [] }) {
    return (
        <div className="tags-container">
            {list.map((value, index) => {
                if (typeof value === 'object') {
                    return null;
                }
                return (
                    <div key={value}>
                        <Link className="post-tag-link" to={`/tags/${value.replace(/\./g, '_')}`}>
                            <span key={value} className="tag-item">
                                {value}
                            </span>
                        </Link>
                        {index < list.length - 1 && `·`}
                    </div>
                );
            })}
        </div>
    );
}

export default Tags;
