import React from 'react';
import {
    EmailShareButton,
    FacebookShareButton,
    LineShareButton,
    TelegramShareButton,
    EmailIcon,
    FacebookIcon,
    LineIcon,
    TelegramIcon
} from 'react-share';

function ShareButton({ url, title = '', description = '' }) {
    const shareTitle = `分享-${title}`;
    const content = description;
    return (
        <div className="share-container">
            <EmailShareButton url={url} subject={shareTitle} body={content}>
                <EmailIcon round size="28" />
            </EmailShareButton>
            <FacebookShareButton url={url}>
                <FacebookIcon round size="28" />
            </FacebookShareButton>
            <LineShareButton url={url}>
                <LineIcon round size="28" />
            </LineShareButton>
            <TelegramShareButton url={url}>
                <TelegramIcon round size="28" />
            </TelegramShareButton>
        </div>
    );
}

export default ShareButton;
