import React from 'react';

function Toc({ list }) {
    function scrollHead({ value }) {
        const selectId = `${value
            .toLowerCase()
            .replace(/\s+/g, '-')
            .replace(/\./g, '')}`;
        window.scroll({
            top: document.getElementById(selectId).offsetTop,
            behavior: 'smooth'
        });
    }
    return (
        <div className="toc-list">
            <div className="title">目錄</div>
            {list.map(({ id, value, depth }) => (
                <div
                    key={value}
                    className={`anchor-item anchor-${depth}`}
                    onClick={() => scrollHead({ value })}
                >
                    {value}
                </div>
            ))}
        </div>
    );
}

export default Toc;
